import React, { Fragment } from 'react';
import {Image} from 'react-bootstrap';
// import ProfileImage from '../../public/Narotam.png';
import CloudImage from '../Images/cloud.png';

const Welcome = () => {
    return (
    <Fragment>
        <div className="top-container overflow-hidden">
        <Image className="top-cloud" src={CloudImage} alt="cloud" />
        <div className="text-center mx-2 px-3">
          <h1 style={{fontSize: "3rem"}}>Hi, I'm Narotam Singh</h1>
          <p className="sub-title">Welcome!</p>
        </div>
        <Image className="bottom-cloud" src={CloudImage} alt="cloud" />
      </div>
      <div className="middle-container" id="About">
        <div className="profile">
          <Image className="mx-auto d-block"
            src="https://narotamji.github.io/io/Images/Narotam.png"
            alt="Profile image"
          />
          <h2 className="text-center mt-3">Hello!</h2>
          <p className="w-75 mx-auto text-center">
            I am a Data Administrator at Harpreet Network. and now I am learning
            coding just to upgrade myself and I ❤ Coffee.
          </p>
        </div>
        </div>
    </Fragment>
    )
}

export default Welcome
